import Data from "@/js/core/data";

const Shortcut_Server = "https://api.500apps.com/shortcuts/shortcut";

class Shortcut extends Data {
  constructor() {
    super(Shortcut_Server);
    super.disablePCors();
  }
  async get(roomname) {
    this.url = `${Shortcut_Server}/${roomname}`;
    return await super.get();
  }
}

export default new Shortcut();
