<template>
  <div style="height:100vh">
    <VueJitsiMeet ref="jitsiRef" domain="qa.conference.ly" :options="jitsiOptions" v-if="enable"></VueJitsiMeet>
    <iframe id="loggedinId" style="display: none;" src="https://infinity.500apps.com/ls.html"></iframe>
  </div>
</template>
<script>
  import { JitsiMeet } from "@mycure/vue-jitsi-meet";
  import { admin, guest } from "@/js/conference/config";
  import InterfaceConfigJs from "@/js/conference/interface-config.js";
  import Shortcut from "../../js/conference/shortcut";
  import UserJs from "../../js/conference/user";
  export default {
    components: {
      VueJitsiMeet: JitsiMeet,
    },
    data() {
      return {
        roomName: "",
        enable: false,
        config: {},
        roomInfo: {},
        userInfo: {},
      };
    },
    async mounted() {
      // Added listener to get jwt.
      let that = this;
      // If host includes .conference.ly
      if (window.location.host.includes(".conference.ly")) {
        // Add listener
        window.addEventListener("message", async function(event) {
          // If action is jwt_received assign jwt to window.
          if (event.data.action == "jwt_received") {
            window.JWT = event.data.jwt;
            // Decode jwt to get user_id, domain_id and env.
            if (window.JWT) {
              that.userInfo = that.decodeJwt(event.data.jwt);
              // If domain_id and user_id matches then load's modinator else guest config.
              if (that.userInfo.user_id == that.roomInfo.created_by && that.userInfo.tenant_id == that.roomInfo.domain_id) that.config = admin;
              else that.config = guest;
            } else that.config = guest;
            that.setAdminInfo(that.userInfo.user_id, that.userInfo.env);
          }
        });
        // Assign the random room name.
        this.roomName = window.location.host.split(".conference.ly")[0];
        // Get domain_id based on roomname(sub domain)
        let response = await Shortcut.get(this.roomName);
        this.roomInfo = response[0];
        if (response.length) {
          this.loadIframe(response[0].domain_id);
        }
      } else {
        // If it is public room.
        this.roomName = this.$route.params.roomname;
        this.enable = true;
        this.config = guest;
      }
    },
    computed: {
      jitsiOptions() {
        return {
          roomName: this.roomName,
          height: window.innerHeight,
          parentNode: "",
          configOverwrite: this.config,
          interfaceConfigOverwrite: InterfaceConfigJs,
          onload: this.onIFrameLoad,
        };
      },
    },
    methods: {
      // Called once it receive domain_id.
      loadIframe(value) {
        if (!window.JWT)
          setTimeout(() => {
            let element = document.getElementById("loggedinId");
            element.contentWindow.postMessage({ action: "get_jwt", value: value }, "*");
          }, 250);
      },
      // Get image, name and set to jitsi instance.
      async setAdminInfo(id = "", env = "") {
        let response = { pic_path: "", name: "" };
        if (env) {
          response = await UserJs.get(id, env);
        }
        this.enable = true;
        setTimeout(() => {
          this.$refs.jitsiRef.executeCommand("avatarUrl", response.pic_path);
          this.$refs.jitsiRef.executeCommand("displayName", response.name);
        }, 50);
      },
      onIFrameLoad() {
        // Listen after cancel's the call.
        // this.$refs.jitsiRef.jitsiApi.addListener("readyToClose", () => {
        //   window.location.href = `https://infinity.500apps.com/${this.$route.params.appname}`;
        // });
      },
      // Decode Jwt to get region
      decodeJwt(token) {
        let base64Url = token.split(".")[1];
        let base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
        let jsonPayload = decodeURIComponent(
          atob(base64)
            .split("")
            .map(function(c) {
              return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
            })
            .join(""),
        );
        return JSON.parse(jsonPayload);
      },
    },
  };
</script>
