import Data from "@/js/core/data";

const User_Server = "https://api.${region}.500apps.com/v2/users/domain/domain_user";

class Users extends Data {
  constructor() {
    super(User_Server);
    this.disablePCors();
  }
  async get(id, env) {
    // Override the super class method to set headers.
    this.getConfig = () => {
      return {
        headers: {
          Authorization: `Bearer ${window.JWT}`,
          withCredentials: true,
        },
      };
    };
    // eslint-disable-next-line no-unused-vars
    let region = env;
    this.url = eval("`" + `${User_Server}/${id}` + "`");
    // Get user info based on id for avatar & name.
    let response = await super.get();
    return response[0];
  }
}
export default new Users();
